<template>
    <div class="buy-page">
        <div class="buy-c">
            <p class="buy-title">
                {{$t('french.sellPage[0]')}}
                <span class="name">
                    {{ orderData.oop_name }}
                </span>
                <span class="way">
                     {{$t('french.sellPage[1]')}}
                </span>
                {{ parseFloat(orderData.total_num) }} USDT
            </p>
            <div class="order clr">
                <span class="order-num">
                     {{$t('french.sellPage[2]')}}{{ orderData.order_num }}
                </span>
                <span class="order-status">
                    {{ showStatus }}
                </span>
            </div>
            <div class="order-detail">
                <span class="detail-list">{{$t('french.sellPage[3]')}}{{ orderData.total_price }} CNY</span>
                <span class="detail-list">{{$t('french.sellPage[4]')}}{{ parseFloat(orderData.total_num) }} USDT</span>
                <span class="detail-list">{{$t('french.sellPage[5]')}}{{ orderData.price }} CNY</span>
            </div>
            <div class="remark">
                <span>
                    {{$t('french.sellPage[6]')}}
                </span>
            </div>
            <div class="contact">
                <p class="cotact-title">{{$t('french.sellPage[7]')}}</p>
                <p class="someone">{{$t('french.sellPage[8]')}}{{ orderData.oop_name }}</p>
                <p class="phone-number">{{$t('french.sellPage[9]')}}{{ orderData.oop_mobile }}</p>
            </div>
            <div class="paid">
                <p class="paid-title">
                    {{$t('french.sellPage[10]')}}
                </p>
                <!-- 二维码 -->
                <div class="big-code" v-show="codeShow">
                    <img :src="imgUrl" alt="" class="show-code" style="width: 90px; height: 90px;">
                </div>
                <div class="payment-method hidden-radio">
                    <el-radio-group v-model="radio">
                        <el-radio 
                        :label="item.type"
                        v-for="(item, i) in orderData.pay_list"
                        :key="i"
                        style="display: block;">
                            <span class="pay-method">
                            <img :src="getImg(item.type)" alt="" class="pay-img">
                            </span>
                            <span class="card" v-if="item.type == 'backcard'">{{$t('french.sellPage[11]')}}</span>
                            <span class="card-account">{{ item.number }}</span>
                            <span class="card-name">{{ item.name }}</span>
                            <span class="code" @mouseenter="showCode(item.type)" @mouseleave="hiddenCode" v-if="item.type != 'backcard'">
                            <img src="../../../assets/img/frenchImg/code.png" alt="" class="code-img">
                            </span>
                            <p class="card-detail">
                            <span class="card-bank">{{ item.bank }}</span>
                            <span class="bank-detail">{{ item.branch }}</span>
                            </p>
                        </el-radio>
                    </el-radio-group>
                </div>
            </div>
            <!-- 等待对方付款 -->
            <div class="wait-pay" v-if="isShow == 1">
                <p class="countdown">
                    {{$t('french.sellPage[12]')}}{{ orderData.oop_name }} {{$t('french.sellPage[13]')}} <span style="color: #6185e5;"> {{ countdowntime }} </span>{{$t('french.sellPage[14]')}}
                </p>
                <p class="countdown">
                    {{$t('french.sellPage[15]')}}（{{ orderData.refer }}）
                </p>
            </div>    
            <!-- 待放币 -->
            <div class="wait-paid" v-if="isShow == 2">
                <div class="wait-pay">
                <p class="countdown">
                    {{$t('french.sellPage[16]')}}（{{ orderData.refer }}）
                </p>
                <p class="tips-t">
                    <span class="tip-icon">
                        <img src="../../../assets/img/frenchImg/tips.png" alt="" class="tip-img"/>
                    </span>
                    <span class="tip-detail">
                        {{$t('french.sellPage[17]')}}
                        <span class="sign">
                            "{{$t('french.sellPage[18]')}}"
                        </span>
                        {{$t('french.sellPage[19]')}}
                        <span style="color: #6185e5;">
                             {{ payTime }}
                        </span>
                       {{$t('french.sellPage[20]')}}
                    </span>
                </p>
                <p class="tips-b">
                    {{$t('french.sellPage[21]')}}
                </p>
                </div>
                <div class="btn-group">
                    <button class="paid-btn order-btn" @click="handlePaid">
                        {{$t('french.sellPage[22]')}}
                    </button>
                    <button class="cancel-btn order-btn" @click="handleCancel">
                        {{$t('french.sellPage[23]')}}
                    </button>
                </div>
            </div>
            <!-- 申诉中 -->
            <div class="wait-pay" v-if="isShow == 4">
                <p class="cancel-tip">
                    {{$t('french.sellPage[24]')}}（{{ orderData.refer }}）
                </p>
            </div>
            <!-- 订单完成 -->
            <div class="cancel-tip" v-if="isShow == 3 && this.ssCommand == ''">
                <span>
                    {{$t('french.sellPage[25]')}}
                </span>
            </div>
            <!-- 申诉失败+原因 -->
            <div class="cancel-tip" v-if="isShow == 3 && this.ssCommand != ''">
                <p style="color: red; margin-bottom: 20px; font-size: 22px;">
                   {{$t('french.sellPage[58]')}} {{ ssCommand }}
                </p>
                <p>
                    {{$t('french.sellPage[59]')}} {{ backReason }}
                </p>
            </div>
            <!-- 订单取消 -->
            <div class="cancel-tip" v-if="isShow == 5 && this.ssCommand == ''">
                <span>
                   {{$t('french.sellPage[26]')}}
                </span>
            </div>
            <!-- 申诉成功 -->
            <div class="cancel-tip" v-if="isShow == 5 && this.ssCommand != ''">
                <span style="color: green; font-size: 22px;">
                   {{$t('french.sellPage[60]')}}
                </span>
            </div>
            <div class="caveat">
                <p class="caveat-title caveat-all">
                    {{$t('french.sellPage[27]')}}
                </p>
                <p class="caveat-desc caveat-all">
                    {{$t('french.sellPage[28]')}}
                </p>
                <p class="caveat-desc caveat-all">
                    {{$t('french.sellPage[29]')}}
                </p>
                <p class="caveat-desc caveat-all">
                    {{$t('french.sellPage[30]')}}
                </p>
            </div>
            <!-- 提交申诉弹窗 -->
            <el-dialog :title="ssTitle" :visible.sync="dialogVisible">
                <div class="input-warp">
                    <span class="label">
                        {{$t('french.sellPage[32]')}}
                    </span>
                    <el-input  style="width: 75%; vertical-align: middle;" type="textarea" :rows="3" v-model="ssReason" :placeholder="$t('french.sellPage[33]')">
                    </el-input>
                </div>
                <div class="dialog-footer">
                    <button class="btn dialog-btn" @click="submitAllege">
                        {{$t('french.sellPage[34]')}}
                    </button>
                    <button class="cancel-btn dialog-btn" @click="closeDialog">
                        {{$t('french.sellPage[35]')}}
                    </button>
                </div>
            </el-dialog>
            <!-- 确认放币弹窗 -->
            <el-dialog :title="fbTitle" :visible.sync="cancelDialogVisible">
                <div class="input-warp">
                    <span class="label">
                        {{$t('french.sellPage[37]')}}
                    </span>
                    <input  type="password" class="dialog-input"  v-model="tpwd" :placeholder="$t('french.sellPage[38]')"/>
                </div>
                <div class="dialog-footer">
                    <button class="btn dialog-btn" @click="submitSell">
                        {{$t('french.sellPage[39]')}}
                    </button>
                    <button class="cancel-btn dialog-btn" @click="closeDialog">
                        {{$t('french.sellPage[40]')}}
                    </button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import { confirm, postAllege, paymentList, orderDetail } from '../../../api/c2c'
export default {
    name: 'buyPage',
    data() {
        return {
            ssTitle: this.$t('french.sellPage[31]'),
            fbTitle: this.$t('french.buyPage[36]'),
            isShow: 1,
            imgUrl: '',
            ssReason: '',
            backReason: '',
            tpwd: '',
            codeShow: false,
            radio: '',
            radioCancel: '1',
            radioIng: '1',
            dialogVisible: false,
            cancelDialogVisible: false,
            payList: [],
            orderData: {},
            showStatus: this.$t('french.sellPage[41]'),
            payType: '',
            ssType: this.$t('french.sellPage[42]'),
            TimeData: null,
            countdown: null,
            countdowntime: null,
            qr_time: null, // 确认支付倒计时
            payTime: null, // 渲染到页面的倒计时
            setIntervaltime: null,
            set: null, // 交易倒计时定时器
            payedSet: null, // 待放行倒计时
            ssCommand: ''
        }
    },
    created() {
        this.setTime()
    },
    methods: {
        // 5S
        setTime() {
        var _this = this
        _this.getOrderDetail()
        _this.setIntervaltime = window.setInterval(function () {
            _this.getOrderDetail()
        }, 5000)
        },
        getOrderDetail() {
            orderDetail({ order_num: this.$route.query.order_num }).then(res=> {
                if (res.data.status == 200) {
                    // console.log(res.data.data.status);
                    
                    if (res.data.data.command) {
                        this.ssCommand = res.data.data.command
                    }
                    if (res.data.data.ss_reason) {
                        this.backReason = res.data.data.ss_reason
                    }
                    this.orderData = res.data.data
                    this.isShow = this.orderData.status
                    this.payList = res.data.data.pay_list
                    this.TimeData = res.data.data.add_time
                    this.countdown = res.data.data.down_time //订单倒计时
                    this.qr_time = res.data.data.qr_time //确定支付倒计时
                    //						status = res.data.data.status;
                    if (this.orderData.status == 1) {
                        this.isShow = 1
                        this.showStatus = this.$t('french.sellPage[43]')
                    } else if (this.orderData.status == 2) {
                        this.isShow = 2
                        this.showStatus = this.$t('french.sellPage[44]')
                    } else if (this.orderData.status == 3) {
                        this.isShow = 3
                        this.showStatus = this.$t('french.sellPage[45]')
                    } else if (this.orderData.status == 4) {
                        this.showStatus = this.$t('french.sellPage[46]')
                    } else if (this.orderData.status == 5) {
                        this.isShow = 5
                        this.showStatus = this.$t('french.sellPage[47]')
                    } 
                    if (res.data.data.status == 1) {//待对方付款
                        this.chusouSet(
                            res.data.data.status,
                            res.data.data.down_time
                        )
                        return
                    } else if (res.data.data.status == 2) {//待放币
                        this.payed()
                        return
                    } else {
                        if (this.setIntervaltime) {
                            window.clearInterval(this.setIntervaltime)
                        }
                        if (this.set) {
                            window.clearInterval(this.set)
                        }
                        if (this.setIntepayedSetrvaltime) {
                            clearInterval(this.payedSet)
                        }
                        return
                    }
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    return
                }
            })
        },
        // 订单倒计时
        chusouSet (status, time) {
            console.log('执行了');
            
            // 需要两个参数（此时订单的状态，当前剩余时间）
            // 倒计时 执行函数
            var connet = this
            var shijian
            var minute
            connet.set = window.setInterval( () => {
                switch (status) {
                case '1':
                    // shijian = time / 60 / 60
                    minute = time / 60
                    connet.countdowntime =
                    // parseInt(shijian) +
                    // '小时' +
                    parseInt(minute) +
                    this.$t('french.sellPage[48]') +
                    (time % 60) +
                    this.$t('french.sellPage[49]')
                    break;
                case '2':
                    break
                default:
                    window.clearInterval(connet.setIntervaltime)
                    window.clearInterval(connet.set)
                    break;
                }
                time = time <= 0 ? 0 : --time
                if (time == 0) {
                    connet.countdowntime = this.$t('french.sellPage[50]') + '...';
                    clearInterval(connet.set)
                }
            }, 1000)
        },
        // 待自动确认倒计时
        payed () {
            var _this = this
            //关闭交易倒计时 执行这个倒计时时说明交易倒计时已不存在
            window.clearInterval(_this.set)
            //为了防止意外多起定时器 先把自己给干掉
            window.clearInterval(_this.payedSet)
            _this.payedSet = setInterval( () => {
                // var shi = _this.qr_time / 60 / 60
                var minute = _this.qr_time / 60
                _this.payTime =
                // parseInt(shi) +
                // '小时' +
                parseInt(minute) +
                this.$t('french.sellPage[51]') +
                (_this.qr_time % 60) +
                this.$t('french.sellPage[52]')
                _this.qr_time--
                if (_this.qr_time < 0) {
                    _this.qr_time = 0
                    _this.payTime = this.$t('french.sellPage[53]') + '...';
                    clearInterval(_this.payedSet)
                }
            }, 1000)
        },
        getImg(type) {
            if (type == 'wx') {
                return require('../../../assets/img/frenchImg/wechat.png')
            } else if (type == 'alipay') {
                return require('../../../assets/img/frenchImg/alipay.png')
            } else if (type == 'backcard') {
                return require('../../../assets/img/frenchImg/bank.png')
            }
        },
        // 显示二维码
        showCode(type) {
            this.codeShow = true
            for (var i=0; i<this.payList.length; i++) {
                if (type == this.payList[i].type) {
                    this.imgUrl = this.payList[i].img
                }
            }
        },
        // 隐藏二维码
        hiddenCode() {
            this.codeShow = false
        },
        // 确认放币弹窗
        handlePaid() {
            this.cancelDialogVisible = true
        },
        // 确认放币
        submitSell() {
            this.$public.googleVerify().then((r)=> {
                if (this.tpwd == '') {//密码不能为空
                    this.$public.msg(this.$t('french.sellPage[54]'), 'error', this)
                    return
                } else if (!this.$public.pwd(this.tpwd)) {//密码格式不正确
                    this.$public.msg(this.$t('french.sellPage[55]'), 'error', this)
                    this.tpwd = ''
                    return
                } else {
                    confirm({ 
                        order_no: this.orderData.order_num, 
                        tpwd: this.$public.Cryptographiccapital(this.tpwd),
                        account: this.orderData.oop_account }).then(res=> {
                        if (res.data.status == 200) {
                            this.$public.msg(this.$t('french.sellPage[56]'), 'success', this)
                            window.clearInterval(this.payedSet)
                            this.isShow = 3
                            this.getOrderDetail()
                            this.cancelDialogVisible = false
                        } else {
                            this.$public.msg(res.data.msg, 'error', this)
                            this.cancelDialogVisible = false
                            return
                        }
                    })
                }
            }).catch((r)=>{})
        },
        // 提交申诉
        handleCancel() {
            this.dialogVisible = true
        },
        // 确认提交申诉
        submitAllege() {
            postAllege({
                order_num: this.$route.query.order_num,
                refer: this.orderData.refer,
                account: JSON.parse(sessionStorage.getItem('account')),
                shop_account: this.orderData.oop_account,
                ss_type: this.ssType,
                ss_reason: this.ssReason
            }).then(res=> {
                if (res.status == 200) {
                    this.$public.msg(this.$t('french.sellPage[57]'), 'success', this)
                    window.clearInterval(this.payedSet)
                    this.isShow = 4
                    this.getOrderDetail()
                    this.dialogVisible = false
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    this.dialogVisible = false
                    return
                }
            })
        },
        // 清除定时器
        clearTimer() {
           
        },
        // 关闭弹窗
        closeDialog() {
            this.dialogVisible = false
            this.cancelDialogVisible = false
        },
        offTime() {
            var connet = this;
            window.clearInterval(connet.setIntervaltime);
            window.clearInterval(connet.set);
            window.clearInterval(connet.payedSet);
        }
    },
    // 离开页面销毁定时器
    beforeDestroy(to, from, next) {
        this.offTime()
        next()
    }
}
</script>
<style lang="less">
   @import 'sellPage.less';
</style>



